import {Component} from '@angular/core';
import {IonicPage, NavController, NavParams} from 'ionic-angular';
import {TokenServiceProvider} from "../../providers/token-service/token-service";
import {LocalStorageProvider} from "../../providers/local-storage/local-storage";
import {MaskTokenProvider} from "../../providers/mask-token/mask-token";
import {CryptoParamUtilProvider} from "../../providers/crypto-param-util/crypto-param-util";
import {RechargeServiceProvider} from "../../providers/recharge-service/recharge-service";
import {GoogleAnalyticsProvider} from "../../providers/google-analytics/google-analytics";

/**
 * Generated class for the AddToFavorite01Page page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-add-to-favorite01',
  templateUrl: 'add-to-favorite01.html',
})
export class AddToFavorite01Page {
  mobileNo;
  package;
  shortTitle;
  name;
  addToFavoriteError;
  fName;


  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              public tokenService: TokenServiceProvider,
              public storage: LocalStorageProvider,
              public maskTokenUtil: MaskTokenProvider,
              public cryptoUtil: CryptoParamUtilProvider,
              public rechargeService: RechargeServiceProvider,
              public gaProvider : GoogleAnalyticsProvider) {

    this.mobileNo = navParams.get("mobileNo");
    this.package = navParams.get("package");
    this.shortTitle = navParams.get("shortTitle");
    this.name = navParams.get("shortTitle");
    this.addToFavoriteError = "";
  }

  AddToFavoriteAction = function (name) {

    this.addToFavoriteError = "";
    this.fName = name;

    let flag = true;

    if (name == "" || typeof(name) == "undefined") {
      this.addToFavoriteError = "Name can not be blank."
      flag = false;
      return;
    }

    if (flag) {
      let appData = this.storage.retrieveFromStorage("appData");
      let unMaskRefToken = this.maskTokenUtil.getUnMaskVal(appData.refreshToken);
      this.tokenService.getNewToken(unMaskRefToken)
        .then(data => {
          this.onNewTokenGenComplete(data);
        });
    }
  };

  onNewTokenGenComplete = function (data) {

    if (data.status == 0) {
      this.navCtrl.setRoot('ExpirePage');
    }

    else {
      let appData = this.storage.retrieveFromStorage("appData");
      let encMobileNo = this.cryptoUtil.getEncryptVal(this.mobileNo);
      let encPackage = this.cryptoUtil.getEncryptVal(this.package);
      let encName = this.cryptoUtil.getEncryptVal(this.fName);
      let unMaskToken = this.maskTokenUtil.getUnMaskVal(appData.token);
      this.rechargeService.addToFavoriteList(unMaskToken, appData.userId, appData.appId, encMobileNo, encPackage, encName)
        .then(data => {
          this.onFavoriteListAddComplete(data);
        });
    }
  };

  onFavoriteListAddComplete = function (data) {

    if (data.statusCode == 1) {
      this.navCtrl.push('AddToFavorite02Page', {'mobileNo': this.mobileNo});
    }

    else if (data.statusCode == 0) {
      this.addToFavoriteError = data.note;
    }

    else {
      //$log.info("else Loop");
      this.addToFavoriteError = "System Error Contact with Administrator.";
    }
  };


  ionViewDidLoad() {
    this.gaProvider.startTrackerWithId("Add to Favourite List");
  }

}



